<script>
  import {mapState} from 'vuex'

  export default {
    computed: {
      ...mapState({
        userStore: state => state.userStore
      })
    },
    methods: {
      handleLogout() {
        this.$store.dispatch('clearAuthUser')
        window.localStorage.removeItem('authUser')
        this.$router.push({name: 'home'})
      }
    }
  }
</script>

<template>
  <div class="wrapper" id="dashboard-wrapper">
    <nav class="navbar navbar-default" v-if="userStore.authUser !== null && userStore.authUser.auth">
      <div class="container-fluid">
        <!-- Brand and toggle get grouped for better mobile display -->
        <div class="navbar-header">
          <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
            <span class="sr-only">Toggle navigation</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <a class="navbar-brand" href="#">Brand</a>
        </div>

        <!-- Collect the nav links, forms, and other content for toggling -->
        <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
          <ul class="nav navbar-nav">
            <li><router-link :to="{name:'dashboard'}">Dashboard</router-link></li>
            <li><router-link :to="{name:'editor'}">Editor</router-link></li>
            <li><router-link :to="{name:'pinpassword'}">User Settings</router-link></li>
            <li><router-link :to="{name:'callforwarding'}">Call Forwarding</router-link></li>
          </ul>
          <ul class="nav navbar-nav navbar-right">
            <li><a v-on:click="handleLogout()">Logout</a></li>
              </ul>
            </li>
          </ul>
        </div><!-- /.navbar-collapse -->
      </div><!-- /.container-fluid -->
    </nav>
  </div>
</template>

<style>

</style>



